.Organizations {
    flex: 1 1 0px;
    display: flex;
    flex-direction: column;
    min-height: 0;
    /* overflow-y: auto; */
}

.Organizations .row {
    flex: 0 0 auto;
    /* border: 1px silver dashed; */
    /* border-bottom: 1px rgba(200, 200, 200, 0.8) solid; */
    padding: 4px;
    margin: 4px 0 20px;
    display: flex;
    flex-direction: row;
}

.Organizations .row:hover {
    background-color: rgba(200, 200, 200, 0.5)
}

.Organizations .row>.col1 {
    flex: 1 1 0px;
    margin-left: 10px;
}

.Organizations .row>.col2 {
    flex: 0 0 64px;
}

.Organizations .orgName {
    font-weight: bold;
    font-size: 120%;
    cursor: pointer;
}

.Organizations .orgOwner {}

.Organizations .orgOwner .field-title {
    display: inline-block;
}

.Organizations .orgOwner .field-value {
    display: inline-block;
}

.Organizations .Organizations-info-table {
    width: 100%
}

.Organizations .Organizations-info-table th {
    text-align: right;
    font-weight: bold;
    color: rgba(150, 150, 150, 1);
    width: 8em;
    vertical-align: top;
}

.Organizations .Organizations-info-table td {
    /* width: 50% */
    vertical-align: top;
}